<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 类型编码 -->
            <a-form-model-item
              label="类型编码"
              prop="typeCode"
              ref="typeCode"
            >
              <a-input
                v-model="entity.typeCode"
                placeholder="请输入类型编码，格式为T开头加4位数字"
                name="typeCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 类型名称 -->
            <a-form-model-item
              label="类型名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入类型名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 产品分类 -->
            <a-form-model-item
              label="产品分类"
              prop="category"
              ref="category"
            >
              <a-select
                v-model="entity.category"
                placeholder="请选择产品分类"
                name="category"
              >
                <a-select-option value="电子设备">电子设备</a-select-option>
                <a-select-option value="智能家居">智能家居</a-select-option>
                <a-select-option value="网络设备">网络设备</a-select-option>
                <a-select-option value="办公设备">办公设备</a-select-option>
                <a-select-option value="安防设备">安防设备</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属产品家族 -->
            <a-form-model-item
              label="所属产品家族"
              prop="familyId"
              ref="familyId"
            >
              <a-select
                v-model="entity.familyId"
                placeholder="请选择所属产品家族"
                name="familyId"
                @change="handleFamilyChange"
              >
                <a-select-option
                  v-for="family in familyList"
                  :key="family.id"
                  :value="family.id"
                >
                  {{ family.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 型号 -->
            <a-form-model-item
              label="型号"
              prop="modelNumber"
              ref="modelNumber"
            >
              <a-input
                v-model="entity.modelNumber"
                placeholder="请输入产品型号"
                name="modelNumber"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 规格说明 -->
            <a-form-model-item 
              label="规格说明" 
              prop="specification"
              ref="specification"
            >
              <a-textarea
                v-model="entity.specification"
                placeholder="请输入规格说明"
                :rows="3"
                :max-length="500"
                name="specification"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 类型描述 -->
            <a-form-model-item 
              label="类型描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入类型描述"
                :rows="3"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 技术参数 -->
      <g-collapse headerName="技术参数">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-param-btn" type="primary" icon="plus" @click="addParameter">添加参数</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="parameterColumns" 
                :dataSource="entity.parameters" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="paramName" slot-scope="text, record, index">
                  <a-input v-model="entity.parameters[index].paramName" placeholder="请输入参数名称" />
                </template>
                <template slot="paramValue" slot-scope="text, record, index">
                  <a-input v-model="entity.parameters[index].paramValue" placeholder="请输入参数值" />
                </template>
                <template slot="paramUnit" slot-scope="text, record, index">
                  <a-input v-model="entity.parameters[index].paramUnit" placeholder="请输入单位" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button type="danger" icon="delete" size="small" @click="() => removeParameter(index)"></a-button>
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditProductType",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        typeCode: "",
        name: "",
        category: "",
        familyId: undefined,
        familyName: "",
        modelNumber: "",
        specification: "",
        description: "",
        status: "启用",
        parameters: [],
        productCount: 0,
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      familyList: [], // 产品家族列表
      rules: {
        typeCode: [
          { required: true, message: "请输入类型编码", trigger: "change" },
          { pattern: /^T\d{4}$/, message: "类型编码必须为T开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入类型名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "类型名称不能包含前后空格", trigger: "change" }
        ],
        category: [
          { required: true, message: "请选择产品分类", trigger: "change" }
        ],
        familyId: [
          { required: true, message: "请选择所属产品家族", trigger: "change" }
        ],
        modelNumber: [
          { required: true, message: "请输入产品型号", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      parameterColumns: [
        {
          title: "参数名称",
          dataIndex: "paramName",
          key: "paramName",
          width: "30%",
          scopedSlots: { customRender: "paramName" }
        },
        {
          title: "参数值",
          dataIndex: "paramValue",
          key: "paramValue",
          width: "30%",
          scopedSlots: { customRender: "paramValue" }
        },
        {
          title: "单位",
          dataIndex: "paramUnit",
          key: "paramUnit",
          width: "20%",
          scopedSlots: { customRender: "paramUnit" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  created() {
    this.fetchFamilyList();
  },
  
  methods: {
    // 获取产品家族列表
    fetchFamilyList() {
      // 模拟获取产品家族列表
      setTimeout(() => {
        this.familyList = [
          { id: 1, name: "华为手机系列", manufacturer: "华为" },
          { id: 2, name: "小米家电系列", manufacturer: "小米" },
          { id: 3, name: "联想电脑系列", manufacturer: "联想" },
          { id: 4, name: "戴尔服务器系列", manufacturer: "戴尔" },
          { id: 5, name: "惠普打印系列", manufacturer: "惠普" }
        ];
      }, 300);
    },
    
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取产品类型数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        typeCode: "",
        name: "",
        category: "",
        familyId: undefined,
        familyName: "",
        modelNumber: "",
        specification: "",
        description: "",
        status: "启用",
        parameters: [],
        productCount: 0,
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保parameters数组被正确初始化
          parameters: row.parameters || []
        };
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        // 检查参数表，确保每个参数都有名称和值
        const invalidParameter = this.entity.parameters.find(
          param => !param.paramName || !param.paramValue
        );
        
        if (invalidParameter) {
          this.$message.error("所有技术参数必须有名称和值");
          return;
        }

        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 处理产品家族变更
    handleFamilyChange(value) {
      const family = this.familyList.find(f => f.id === value);
      if (family) {
        this.entity.familyName = family.name;
      }
    },
    
    addParameter() {
      this.entity.parameters.push({
        id: Date.now(), // 使用时间戳作为临时ID
        paramName: "",
        paramValue: "",
        paramUnit: ""
      });
    },
    
    removeParameter(index) {
      this.entity.parameters.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-param-btn {
  margin-bottom: 16px;
}
</style> 